.login-wrap{
    width: 100%;
    height: 100%;
    background-color: white;
    padding-top: 100px;
    color : black;
    font-family: "Gmarket Sans TTF";
    font-weight: bold;
    text-decoration: none;
    
}
.login-box{
    width: 500px;
    height: 600px;
    border: 8px solid #293277;
    border-radius: 15px;
    margin: auto;
    transform: (50% 50%);
    padding: 50px;
}
.login-box h2{
    font-size: 40px;
    text-align: center;
}
.input-wrap{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.input-value-wrap{
    margin-bottom: 40px;
}
.input-value-wrap p{
    font-size: 20px;
}
.input-value-wrap input{
    width: 400px;
    height: 50px;
    border-radius: 15px;
    border: 5px solid #293277;
    font-size: 25px;
}
.submit-wrap{
    margin-top: 30px;
}
.submit-wrap button{
    width: 414px;
    height: 60px;
    border-radius: 15px;
    background: #293277;
    color : white;
    font-size: 20px;
    font-family: "Gmarket Sans TTF";
    font-weight: bold;
    text-decoration: none;
}